        {name: "渐变-29", colorValueHex: "#f0ff00 To #67d4e1",bgi:"linear-gradient(to bottom right, #f0ff00, #67d4e1)"},
        {name: "渐变-30", colorValueHex: "#67fe99 To #0fe8f0",bgi:"linear-gradient(to bottom right, #67fe99, #0fe8f0)"},

      ]
    }
  },
  methods: {
    changeToRed() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("红") !== -1;
      });
    },
    // changeToAll() {
    //   this.filtColorList = this.colorList.filter(p => {
    //     return p.name.indexOf("") !== -1;
    //   });
    // },
    changeToPurple() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("紫") !== -1;
      });
    },
    changeToGray() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("灰") !== -1;
      });
    },
    changeToWhite() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("白") !== -1;
      });
    },
    changeToBlue() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("蓝") !== -1;
      });
    },
    changeToCyan() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("青") !== -1;
      });
    },
    changeToGreen() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("绿") !== -1;
      });
    },
    changeToYellow() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("黄") !== -1;
      });
    },
    copyHex(hexValue){
      var value=hexValue;
      console.log(value)
      window.clipboardData.setData("Text",value);
    },
    changeToChange(){
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("渐变") !== -1;
      });

    }
  },
  computed:{
    years(){
      var date=new Date()
      let yeaynow=date.getFullYear()
      let nextYear=yeaynow+1
      return yeaynow+"~"+nextYear
    }
  }

}
